/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import { RenderEditableProps, Controller } from '@ali/4ever-cangjie';
import { HighlightStyle } from './styled';
import { IHoverBlockConfig } from './types';

export default (config: IHoverBlockConfig) => {
  return (props: RenderEditableProps, controller: Controller, next) => {
    if (config.isHighlightBorder) {
      return (
        <>
          {next()}
          <HighlightStyle />
        </>
      );
    }
    return next();
  };
};
